import { Component } from 'react'

class CreateOrder extends Component {
	render() {
		return(
			<form className="createOrder">
				<h1> </h1>
				<div><span> </span><input type="text" name="title" placeholder="" /></div>
				<div><span> </span><input type="text" name="User" placeholder="" /></div>
				<div><span> </span><input type="text" name="phone" placeholder="8 (999) 999 99 99" /></div>
				<div><span> </span><input type="date" name="date" placeholder="" /></div>
				<button> </button>
			</form>
		)
	}
}

export default CreateOrder